@import url('https://fonts.googleapis.com/css?family=Lato:700|Montserrat:600&display=swap');

.home {
  background-color: #F8F9FE;
  width: 100%;
  min-height: 50em;
  height: 100vh;
}

.homeComponents {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-family: 'Lato', sans-serif;
  font-size: 13px;
  padding: 0px 210px;
  position: relative;
}

.introText {
  text-align: left;
  width: 80%;
  float: left;
  margin: 0;
  color: rgb(47, 51, 66);
}

.introText h1 {
  margin: 0;
  font-size: 40px;
  padding-top: 70px;
  padding-left: 4px;
  letter-spacing: 5px;
}

.introText h2 {
  margin: 0;
  letter-spacing: 5px;
  padding-top: 10px;
  font-size: 50px;
}

.introText h3 {
  margin: 0;
  font-size: 20px;
  padding-left: 4px;
  letter-spacing: 1px;
}

.scrollImage {  
  position: relative;  
  height: 50px;
  width: 55px;
}
.scrollButton {
  position: relative;  
  top: 90%;
  float: right;
  height: 20px;
  width: 15px;
  margin: 0px 80px;
}